<template>
  <div>
    {{msg}}
  </div>
</template>

<script>
export default {
  data() {
    return {
      msg: 'test'
    }
  },
  methods: {
    
  },
  async created(){
    let test = await global.ajax.request('/demo');
    this.msg = test.data.msg;
  },
}
</script>




